
import { LitElement, html, css } from '@polymer/lit-element'
import { render } from 'lit-html'
import {anchorRoute, gotoRoute} from './../Router'
import Auth from './../Auth'
import App from './../App'
import UserAPI from './../UserAPI'
import Toast from './../Toast'

customElements.define('va-haircut', class Haircut extends LitElement {
  constructor(){
    super()    
  }

  static get properties(){
    return {
      id: {
        type: String
      },
      name: {
        type: String
      },
      description: {
        type: String
      },
      price: {
        type: String
      },
      user: {
        type: Object
      },
      image: {
        type: String
      },
      gender: {
        type: String
      },
      length: {
        type: String
      }

    }
  }

  firstUpdated(){
    super.firstUpdated()
  }

//   This handles what will happen when you click the "More Info" button 
  moreInfoHandler(){
    //***   alert("This is where the description will go")
    // Create dialog
      const dialogEl = document.createElement('sl-dialog')

    // Add class name. Make sure className letter cases are correct. You had a mistake here. 
      dialogEl.className = 'haircut-dialog'

    //   sl-dialog content. This is all the info that will pop-up in the dialog 
      const dialogContent = html 
      
    //   .price uses --brand-color in _vars.scss
      `
        <style>
        .wrap {
            display: flex;
        }
        .image {
            width: 50%;
        }
        .image img {
            width: 100%;
        }
        .content {
            padding-left: 1em;
        }
        .gender span,
        .length span {
            text-transform: uppercase;
            font-weight: bold;
        }

        .price {
            font-size: 1.5em;
            color: var(--brand-color);
        }

        </style>
        <div class="wrap">
        <div class="image">
            <img src="${App.apiBase}/images/${this.image}" alt="${this.name}" />
        </div>
        <div class="content">
            <h1>${this.name}</h1>
            <p>${this.description}</p>
            <p class="price">$${this.price}</p>
            <p class="gender">Gender: <span>${this.gender}</span></p>
            <p class="length">Length: <span>${this.length}</span></p>
        
            <sl-button @click=${this.addFavHandler.bind(this)}>
            <sl-icon slot="prefix" name="heart-fill"></sl-icon>
            Add to Favourites
            </sl-button>
        </div>
        </div>
      `

      render(dialogContent, dialogEl)

    //   Append/move? to document body
      document.body.append(dialogEl)

    //   Show sl-dialog
      dialogEl.show()

    //   Once the user hides the dialogue for the more info, delete it 
    dialogEl.addEventListener('sl-after-hide', () => {
        dialogEl.remove()
    })

  }

  

  //*** addFavHandler(){
  //***   alert("You just added this to favourites!")
  //*** }

async addFavHandler(){    
  try {
    await UserAPI.addFavHaircut(this.id)
    Toast.show('Haircut added to favourites')
  }catch(err){
    Toast.show(err, 'error')
  }
}

  
  render(){    
    return html`
    <style>
        /* COMMENT */
        /* This .author is called in the author below. This is the styling for it */
        .author {
            font-size: 0.9em; 
            font-style: italic; 
            opacity: 0.8; 
        }
    </style>

<!-- COMMENT -->
<!-- This is in charge of what appears on va-haircut which is called in home.js -->
    <sl-card>
        <img slot="image" src="${App.apiBase}/images/${this.image}" />
        <h2>${this.name}</h2>
        <h3>$${this.price}</h3>
        <p class="author">Haircut by ${this.user.firstName} ${this.user.lastName}</p>
        <!-- moreInfoHandler is declared at the top. On click, moreInfoHandler will play -->
        <sl-button @click=${this.moreInfoHandler.bind(this)}>More Info</sl-button>
            <!-- addFavHandler is declared at the top. On click, addFavHandler will play -->
        <sl-icon-button name="heart-fill" label="Add to Favourites" @click=${this.addFavHandler.bind(this)}></sl-icon-button>
        </sl-card>
 
    `
  }
  
})

