import App from './../../App'
import {html, render } from 'lit-html'
import {gotoRoute, anchorRoute} from './../../Router'
import Auth from './../../Auth'
import Utils from './../../Utils'
import HaircutAPI from './../../HaircutAPI'
import Toast from '../../Toast'

// When you go on website/haircuts -> initiate all of these
class HaircutsView {
  async init(){
    document.title = 'Haircuts'    
    this.haircuts = null
    this.render()    
    Utils.pageIntroAnim()
    await this.getHaircuts()
    // this.filterHaircuts('price', '20-25')
  }

  async filterHaircuts(field, match){
    // Validate
    if(!field || !match) return

    // Get fresh copy of the haircuts list for filtering 
    this.haircuts = await HaircutAPI.getHaircuts()

    let filteredHaircuts

    // Gender
    if(field == 'gender'){
      filteredHaircuts = this.haircuts.filter
      (haircut => haircut.gender == match)
      // this.haircuts = filteredHaircuts
      // this.render()
    }

      // Length
      if(field == 'length'){
        filteredHaircuts = this.haircuts.filter
        (haircut => haircut.length == match)
        // this.haircuts = filteredHaircuts
        // this.render()
      }

      // Price
      if(field == 'price'){
        // Get priceRangeStart
        const priceRangeStart = match.split('-')[0]
        const priceRangeEnd = match.split('-')[1]
        // console.log(priceRangeStart, priceRangeEnd)
        filteredHaircuts = this.haircuts.filter
        (haircut => haircut.price >= priceRangeStart && haircut.price 
        <= priceRangeEnd)
        // (haircut => haircut.price == match)
        // this.haircuts = filteredHaircuts
        // this.render()
      }

      // Render
      this.haircuts = filteredHaircuts
      this.render()

  }

  clearFilterBtns(){
    const filterBtns = document.querySelectorAll('.filter-btn')
    filterBtns.forEach(btn => btn.removeAttribute("type") )
  }

  handleFilterBtn(e){
    // console.log(e.target)
    // Clear all filter buttons active type primary
    this.clearFilterBtns()
    // Set button active (type = primary)
    e.target.setAttribute("type", "primary")
    // Extract the field and match from the button 
    const field = e.target.getAttribute("data-field")
    const match = e.target.getAttribute("data-match")
    // console.log("field = ", field)
    // console.log("match = ", match)

    //Filter haircuts 
    this.filterHaircuts(field, match)
  }

  clearFilters(){
    this.getHaircuts()
    this.clearFilterBtns()
  }

  //*** This is to filter female haircuts 
  //*** filterHaircutsGenderFemale(){
  //***   const filteredHaircuts = this.haircuts.filter(haircut => haircut.gender == 'm')
  //***   console.log("One sec! Filtering array ...")
  //***   console.log(filteredHaircuts) 

  async getHaircuts(){
      try{
          this.haircuts = await HaircutAPI.getHaircuts()
          console.log(this.haircuts)
          this.render()
      }catch(err){
          Toast.show(err, 'error')
      }
  }

  render(){
    const template = html`

      <style>
        .filter-menu {
          display: flex;
          align-items: center;
        }
        .filter-menu > div {
          margin-right: 1em;
        }
      </style>

      <va-app-header title="Haircuts" user="${JSON.stringify(Auth.currentUser)}"></va-app-header>
      <div class="page-content">        

        <div class="filter-menu">
        <div>
        Filter by 
        </div>
<!--------------GENDER----------------->
        <div>
        <strong>Gender</strong>

        <sl-button 
        class="filter-btn" size="small" data-field="gender" 
        data-match="m" @click=${this.handleFilterBtn.bind(this)}>
        M
        </sl-button>

        <sl-button 
        class="filter-btn" size="small" data-field="gender" 
        data-match="f" @click=${this.handleFilterBtn.bind(this)}>
        F
        </sl-button>

        <sl-button 
        class="filter-btn" size="small" data-field="gender" 
        data-match="u" @click=${this.handleFilterBtn.bind(this)}>
        U
        </sl-button>

        </div>
<!--------------GENDER----------------->

<!--------------LENGTH----------------->
<div>
<strong>Length</strong>

<sl-button 
class="filter-btn" size="small" data-field="length" 
data-match="s" @click=${this.handleFilterBtn.bind(this)}>
S
</sl-button>

<sl-button 
class="filter-btn" size="small" data-field="length" 
data-match="m" @click=${this.handleFilterBtn.bind(this)}>
M
</sl-button>

<sl-button 
class="filter-btn" size="small" data-field="length" 
data-match="l" @click=${this.handleFilterBtn.bind(this)}>
L
</sl-button>

</div>
<!--------------LENGTH----------------->

<!--------------PRICE----------------->
<div>
<strong>Price</strong>

<sl-button 
class="filter-btn" size="small" data-field="price" 
data-match="10-20" @click=${this.handleFilterBtn.bind(this)}>
$10-20
</sl-button>

<sl-button 
class="filter-btn" size="small" data-field="price" 
data-match="20-30" @click=${this.handleFilterBtn.bind(this)}>
$20-30
</sl-button>

<sl-button 
class="filter-btn" size="small" data-field="price" 
data-match="30-40" @click=${this.handleFilterBtn.bind(this)}>
$30-40
</sl-button>

</div>
<!--------------PRICE----------------->

        <div>
        <sl-button size="small" @click=${this.clearFilters.bind(this)}>
        Clear Filters
        </sl-button>
        </div>
        </div>


        <!-- 
        <sl-button 
        class="filter-btn" size="small" data-field="gender" 
        data-match="m" 
        @click=${this.handleFilterBtn}>M
        <sl-button>

        <sl-button 
        class="filter-btn" size="small" data-field="gender" 
        data-match="f" 
        @click=${this.handleFilterBtn}>F
        <sl-button>

        
        <sl-button 
        class="filter-btn" size="small" data-field="gender" 
        data-match="u" 
        @click=${this.handleFilterBtn}>U
        <sl-button>
 -->


 
<!-- Creating the haircut grids on the haircut list -->

<!-- This div class is targeted in _base.scss of frontend -->
        <div class="haircuts-grid">

        ${this.haircuts == null ? html`
            <sl-spinner></sl-spinner>
        ` : html`
            ${this.haircuts.map(haircut => html`

            <va-haircut class="haircut-card"
             id="${haircut._id}"
              name = "${haircut.name}"
              description = "${haircut.description}"
              price = "${haircut.price}"
              user = ${JSON.stringify(haircut.user)}
              image = "${haircut.image}"
              gender = "${haircut.gender}"
              length = "${haircut.length}"
            >
            </va-haircut>

            <!-- This div class is targeted in _base.scss of frontend
            <sl-card class="haircut-card">
                <img src="${App.apiBase}/images/${haircut.image}" alt="${haircut.name}" slot="image" />
                <h2>${haircut.name}</h2>
                <!-- Extra $ for the price
                <h3>$${haircut.price}</h3>
                <p>${haircut.description}</p>
                <p>By ${haircut.user.firstName} ${haircut.user.lastName} </p>
            </sl-card>
            -->
            
            `)}

            `}

        </div>

      </div>      
    `
    render(template, App.rootEl)
  }
}


export default new HaircutsView()

