import App from './../../App'
import {html, render } from 'lit-html'
import {gotoRoute, anchorRoute } from './../../Router'
import Auth from './../../Auth'
import Utils from './../../Utils'

class HomeView {
  init(){    
    console.log('HomeView.init')
    document.title = 'Home'    
    this.render()    
    Utils.pageIntroAnim()    
  }

  render(){
    const template = html`
      <va-app-header title="Home" user=${JSON.stringify(Auth.currentUser)}></va-app-header>
      
      <div class="page-content">
        <h1 class="anim-in">Hey ${Auth.currentUser.firstName}</h1>

        <h3>Button example:</h3>
        <sl-button class="anim-in" @click=${() => gotoRoute('/profile')}>View Profile</sl-button>
        <p>&nbsp;</p>
        <h3>Link example</h3>
        <a href="/profile" @click=${anchorRoute}>View Profile</a>
        <p></p>
        

<!-- va-haircut is fromt frontend/src/components/va-haircut.js -->
<!-- In frontend/src/index.js | We imported va-haircut so we can then use this anywhere in project like done below  -->
<!-- These properties (name, desc, price) are declaared in va-haircut.js  -->
<!--
        <va-haircut name="Ace Ventura"
        
        name = "Ace Ventura"
        description = "Get this haircut if you are in the mood to save some pets"
        price = "30"
        user = ${JSON.stringify({firstName: 'Jet', lastName: 'Trijo'})}
        image = "https://evoke.ie/wp-content/uploads/2019/12/Jim-1.jpg"
        gender = "m"
        length = "m"
        >

        </va-haircut>
-->

      </div>
     
    `
    render(template, App.rootEl)
  }
}

export default new HomeView()